<template>
 <div>
    <div class="adminlogin_container">
      <div class="login_box">
        <!--  -->
        <div class="avatar_box">
          <img src="../assets/logo.png" alt="">
        </div>
        <!--  -->
        <el-form ref="LoginFormRef" :model="loginForm" label-width="0" :rules="LoginFormRules" class="login_form">
          <el-form-item prop="username">
            <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
           
          </el-form-item>
        </el-form>  
      </div>
  </div>
 </div>
  
</template>

<script>
import {mapActions} from 'vuex'
export default {
  
    data() {
      return {
        loginForm:{
          username:'',
          password:''
        },
        LoginFormRules:{
          username:[
            { required: true, message: '', trigger: 'blur' },
          ],
          password:[
            { required: true, message: '', trigger: 'blur' },
          ]
        }
      }
    },
    methods: {
      ...mapActions(['setSeller','setAdmin','setAdmin2']),
     register(){
         //

     },
      login() {
        this.$refs['LoginFormRef'].validate(async (valid) => {
          if (valid) {
           
              this.$axios.post('http://8.141.56.170:9100/admin/login',{
                username:this.loginForm.username,
                password:this.loginForm.password
              }).then(res=>{
               

                if(res.data.status == 200){
                  this.$axios.post('http://8.141.56.170:9100/admin/finduserbyname',{
                    username:this.loginForm.username
                  }).then(res1=>{
                    console.log(res1.data.data[0].roleid)
                     if(res1.data.data[0].roleid == 3){
                       localStorage.setItem('isadmin',this.loginForm.username)
                       this.setAdmin(this.loginForm.username)
                       console.log(this.$store.state.usr.isadmin)
                       
                     }
                     if(res1.data.data[0].roleid == 2){
                       this.setAdmin2(this.loginForm.username)
                       localStorage.setItem('isadmin2',this.loginForm.username)
                       
                     }
                     if(res1.data.data[0].roleid == 1){
                       this.setSeller(this.loginForm.username)
                       localStorage.setItem('seller',this.loginForm.username)
                     }
                     

                  })
                  console.log(res)
                  this.$router.push('adminHome')
                 
                  
                }
                else{
                  this.$alert(res.data.message)
                }
              })
              //
              
              
              
              
            }
              
            
           else  {
          
           
            return false
          }
        })
      }
    }
   }
</script>

<style >
.adminlogin_container .login_container{
  background-color: #2b4b6b;
  height: 100%;
}

.adminlogin_container .login_box{
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%)
}

.adminlogin_container .avatar_box{
  width: 130px;
  height: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left:50%;
  transform: translate(-50%,-50%);
  background-color: #fff;}
 .adminlogin_container img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }


.adminlogin_container .login_form{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.adminlogin_container .btns{
  display: flex;
  justify-content:flex-end;
}

</style>