<template>
 <div class="collect">
   
    <div class="collect-header">
      <div class="collect-title">
        <i class="el-icon-collection-tag" style="color: #ff6700;"></i>
        
      </div>
    </div>
    <div class="content">
      <div class="goods-list" v-if="collectList.length>0">
        <List :list="collectList" :isDelete="true"></List>
      </div>
      <!--  -->
      <div v-else class="collect-empty">
        <div class="empty">
          <h2></h2>
          <p></p>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials  = true;
import List from '../components/List'
export default {
    components:{List},
   
    data() {
        return {
            collectList:[]
            
        };
    },
    mounted() {
      
      
    },
    methods: {

        
    },
    created(){
     
      axios.get('http://8.141.56.170:9100/user/collect/queryall',{
        params:{
          limit:100,
          page:1
        }
      }).then(res=>{
       let  idList = []
        for(var j = 0;j<res.data.data.length;j++){
          idList[j] = res.data.data[j].id
          

        }
         
        console.log(res)    
         var low = 0
        for(var i = 0;i<res.data.data.length;i++){
          this.$axios.get('http://8.141.56.170:9100/product-detail/'+res.data.data[i].commid).then(res1=>{
            
           var temp = res1.data.data
          
        
           this.$set(temp, 'id', idList[low]);

           this.collectList.unshift(temp)
           low++
          
          }
            
          )
        }
        
        console.log('sss')
        console.log(this.collectList)
        
      })
    }
};
</script>

<style >
.collect {
  background-color: #f5f5f5;
}
.collect .collect-header {
  height: 64px;
  background-color: #fff;
  border-bottom: 2px solid #ff6700;
}
.collect .collect-header .collect-title {
  width: 1225px;
  margin: 0 auto;
  height: 64px;
  line-height: 58px;
  font-size: 28px;
}
.collect .content {
  padding: 20px 0;
  width: 1225px;
  margin: 0 auto;
}
.collect .content .goods-list {
  margin-left: -13.7px;
  overflow: hidden;
}
/* CSS */
.collect .collect-empty {
  width: 1225px;
  margin: 0 auto;
}
.collect .collect-empty .empty {
  height: 300px;
  padding: 0 0 130px 558px;
  margin: 65px 0 0;
  background: url(../assets/imgs/cart-empty.png) no-repeat 124px 0;
  color: #b0b0b0;
  overflow: hidden;
}
.collect .collect-empty .empty h2 {
  margin: 70px 0 15px;
  font-size: 36px;
}
.collect .collect-empty .empty p {
  margin: 0 0 20px;
  font-size: 20px;
}

</style>