<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
       
    </el-breadcrumb>

    <el-card>
      <el-alert
        title=""
        :closable="false"
        center
        show-icon
        type="info"
      >
      </el-alert>
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
         
        <el-step title=""></el-step>
      </el-steps>

      <el-form
        ref="addFormRef"
        :model="addForm"
        :rules="addFormRules"
        label-width="80px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :before-leave="beforTabLeave"
          :tab-position="'left'"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_oriprice">
              <el-input v-model="addForm.goods_oriprice" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_thinkmoney">
              <el-input v-model="addForm.goods_thinkmoney" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                :options="catelist"
                :props="cateProps"
                clearable
                v-model="addForm.goods_cat"
                @change="CateChanged"
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTabelData"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="cb"
                  border
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTabelData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
           action="http://8.141.56.170:9100/relgoods/video"
              :before-upload  = "uploadimg"
              :headers="headerobj"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <el-dialog
      title=""
      :visible.sync="previewVisible"
      width="50%">
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>


export default {
  data() {
    return {
      activeIndex: "0",
      addForm: {
        goods_name: "",
        goods_oriprice: 0,
        goods_thinkmoney: 0,
        goods_number: 0,
        goods_cat: [],
        pics:[],
        goods_introduce:'',
        attrs:[]
      },
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_wight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      catelist:[ {cat_deleted: false,cat_id: 1,cat_level: 0,cat_name: "3C",cat_pid: 0},
      {cat_deleted: false,
cat_id: 1,
cat_level: 0,
cat_name: "",
cat_pid: 1},{cat_deleted: false,
cat_id: 2,
cat_level: 3,
cat_name: "",
cat_pid: 2}
],
      cateProps: {
        expandTrigger: "hover",
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      manyTabelData: [],
      onlyTabelData: [],
      headerobj:{
        Authorization:window.sessionStorage.getItem('token')
      },
      previewPath:'',
      previewVisible:false
    };
  },
  methods: {
  
    handlePreview(file) {
      this.previewPath = file.response.data.src
      console.log(file.response.data.src)
      this.previewVisible = true
    },
    handleRemove(fire) {
      const picPath = fire.response.data.src
      const i = this.addForm.pics.findIndex(x=>x.pic===picPath)
      this.addForm.pics.splice(i,1)
    },
    handleSuccess(response) {
        console.log(response)
      const picInfo = {pic:response.data.src}
      this.addForm.pics.push(picInfo)
    },
    uploadimg(file){
      console.log('upload')
      this.$axios.post('http://8.141.56.170:9100/relgoods/video',{
        file:file
      }).then(res=>{
          res
        console.log(res)
      })
     


    },
    add() {

      this.$axios.post('http://8.141.56.170:9100/relgoods/rel',{
        commname:this.addForm.goods_name,
        commdesc:'',
        orimoney:this.addForm.goods_oriprice,
        thinkmoney:this.addForm.goods_thinkmoney,
        category:this.addForm.goods_cat

      }).then(res=>{
        console.log(res)
      })

      
    return true
    }
  },
  created() {
    this.getCateList();
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    }
  }
};
</script>

<style >
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.previewImg{
  width: 100%;
}

.btnAdd{
  margin-top: 15px;
}
</style>
