<template>
     <div id="details">
    <!--  -->
    <div class="page-header">
      <div class="title">
        <p>{{productDetails.product_name}}</p>
        <div class="list">
          <ul>
            <li>
              <router-link to></router-link>
            </li>
            <li>
              <router-link to></router-link>
            </li>
            <li>
              <router-link to></router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>


    <!--  -->
    <div class="main">
      <!--  -->
      <div class="block">
        <el-carousel height="560px" v-if="productPicture.length>1">
          <el-carousel-item v-for="item in productPicture" :key="item.id">
            <img style="height:560px;" :src="$target + item" :alt="item.intro" />
          </el-carousel-item>
        </el-carousel>
        <div v-if="productPicture.length==1">
          <img
            style="height:560px;"
            :src="$target + productPicture[0]"
            :alt="productPicture[0]"
          />
        </div>
      </div>
  

      <!--  -->
      <div class="content">
        <h1 class="name">{{productDetails.commname}}</h1>
        <p class="intro">{{productDetails.commdesc}}</p>
        <p class="store"></p>
        <div class="price">
          <span>{{productDetails.thinkmoney}}</span>
          <span
            v-show="productDetails.thinkmoney != productDetails.orimoney"
            class="del"
          >{{productDetails.orimoney}}</span>
        </div>
        <div class="pro-list">
          <span class="pro-name">{{productDetails.commname}}</span>
          <span class="pro-price">
            <span>{{productDetails.orimoney}}</span>
            <span
              v-show="productDetails.orimoney != productDetails.thinkmoney"
              class="pro-del"
            >{{productDetails.thinkmoney}}</span>
          </span>
          <p class="price-sum"> : {{productDetails.thinkmoney}}</p>
        </div>
        <!--  -->
        <div class="button">
          <el-button class="shop-cart" :disabled="dis" @click="addShoppingCar"></el-button>
          <el-button class="like" @click="addCollect"></el-button>
        </div>
  
      </div>
    </div>
 
  </div>
</template>

<script>
import {mapActions} from 'vuex'
import axios from 'axios'
axios.defaults.withCredentials  = true;
export default {
    
   
    data() {
        return {
             dis: false, // 
      productID: "", // id
      productDetails: "", // 
      productPicture: "" // 
            
        };
    },
    activated() {
    if (this.$route.query.productID != undefined) {
      this.productID = this.$route.query.productID;
    }
  },
  watch: {
    //id
    productID: function(val) {
      this.getDetails(val);
      this.getDetailsPicture(val);
    }
  },
    mounted() {
        
    },
    methods: {
         ...mapActions(["unshiftShoppingCart", "addShoppingCartNum"]),
         ...mapActions(['setShowLogin']),
          // 
    getDetails(val) {
    console.log('datail')
      this.$axios
        .get("http://8.141.56.170:9100/product-detail/"+val )
        .then(res => {
            console.log('datail')
          console.log(res)

          this.productDetails = res.data.data;
          this.productPicture = res.data.data.otherimg
        })
        .catch(err => {
          return Promise.reject(err);
        });
    },
    
      addShoppingCar() {
      // ,
      console.log(1)
      if (!this.$store.getters.getUser) {
       this.$store.dispatch("setShowLogin", true);
        return;
      }
        //
    },
      addCollect() {
      // ,
      if (!this.$store.getters.getUser) {
        this.$store.dispatch("setShowLogin", true);
        return;
      }
      //
      axios.post('http://8.141.56.170:9100/collect/operate',{
        cmuserid:this.productDetails.userid,
        commdesc:this.productDetails.commdesc,
        commid:this.productDetails.commid,
        commname:this.productDetails.commname,
        school:this.productDetails.school,
       
        colloperate:1

      }).then(res=>{
        this.$alert(res.data.message)
      })
   
    }

    },
    
   
};
</script>

<style >
#details .page-header ul li{
    float: right;
    margin: 10px;

}
 #details .block{width: 400px;
 margin-right: 40px;
float: left;}

  #details a {
    text-decoration: none;
    font-size: 20px;
}
#details .main .content .name {
  height: 30px;
  line-height: 30px;
  font-size: 24px;
  font-weight: normal;
  color: #212121;
}
#details .main .content .intro {
  color: #b0b0b0;
  padding-top: 30px;
}
#details .main .content .store {
  color: #ff6700;
  padding-top: 10px;
}
#details .main .content .price {
  display: block;
  font-size: 18px;
  color: #ff6700;
  border-bottom: 1px solid #e0e0e0;
  padding: 25px 0 25px;
}
#details .main .content .price .del {
  font-size: 14px;
  margin-left: 10px;
  color: #b0b0b0;
  text-decoration: line-through;
}
#details .main .content .pro-list {
  background: #f9f9fa;
  padding: 30px 60px;
  margin: 50px 0 50px;
}
#details .main .content .pro-list span {
  line-height: 30px;
  color: #616161;
}
#details .main .content .pro-list .pro-price {
  float: right;
}
#details .main .content .pro-list .pro-price .pro-del {
  margin-left: 10px;
  text-decoration: line-through;
}
#details .main .content .pro-list .price-sum {
  color: #ff6700;
  font-size: 24px;
  padding-top: 20px;
}
#details .main .content .button {
  height: 55px;
  margin: 10px 0 20px 0;
}
#details .main .content .button .el-button {
  float: left;
  height: 55px;
  font-size: 16px;
  color: #fff;
  border: none;
  text-align: center;
}
#details .main .content .button .shop-cart {
  width: 340px;
  background-color: #ff6700;
}
#details .main .content .button .shop-cart:hover {
  background-color: #f25807;
}

#details .main .content .button .like {
  width: 260px;
  margin-left: 40px;
  background-color: #b0b0b0;
}
#details .main .content .button .like:hover {
  background-color: #757575;
}
#details .main .content .pro-policy li {
  float: left;
  margin-right: 20px;
  color: #b0b0b0;
}

</style>