export default {
    state: {
      user: localStorage.getItem('user'), // 
      showLogin: false, // ,
      isadmin:localStorage.getItem('isadmin'), //
      seller:localStorage.getItem('seller'),  //
      roleid:'',
     isadmin2:localStorage.getItem('isadmin2')


    },
    getters: {
      getUser (state) {
        return state.user
      },
      getShowLogin (state) {
        return state.showLogin
      },
      getIsAdmin(state){
        return state.isadmin
      },
      getSeller(state){
        return state.seller
      },
      getRoleid(state){
        return state.roleid
      },
      getAdmin2(state){
        return state.isadmin2
      }

    },
    mutations: {
      setUser (state, data) {
        state.user = data;
      },
      setShowLogin (state, data) {
        state.showLogin = data;
      },
      setAdmin(state,data){
        state.isadmin = data
      },
      setSeller(state,data){
        state.seller = data
      },
      setRoleid(state,data){
        state.roleid = data
      },
      setAdmin2(state,data){
        state.isadmin2 = data
      }
    },
    actions: {
      setUser ({ commit }, data) {
        commit('setUser', data);
      },
      setShowLogin ({ commit }, data) {
        commit('setShowLogin', data);
      },
      setAdmin({ commit },data){
        commit('setAdmin',data);
      },
      setSeller({ commit },data){
        commit('setSeller',data)
      },
      setRoleid({commit},data){
        commit('setRoleid',data)
      },
      setAdmin2({commit},data){
        commit('setAdmin2',data)
      }
    }
  }