<template>
    <div>
         <div id="myLogin">
    <el-dialog title="" width="300px" center :visible.sync="isLogin">
      <el-form :model="LoginUser" :rules="rules" status-icon ref="ruleForm" class="demo-ruleForm">
        <el-form-item prop="userName">
          <el-input prefix-icon="el-icon-user-solid" placeholder="" v-model="LoginUser.userName"></el-input>
        </el-form-item>
        <el-form-item prop="pass">
          <el-input
            prefix-icon="el-icon-view"
            type="password"
            placeholder=""
            v-model="LoginUser.pass"
          ></el-input>

          <el-form-item  prop="imageCode">
            <el-input prefix-icon="el-icon-user-solid" placeholder="" v-model="LoginUser.imageCode"></el-input>
            <img :src='imagesrc' alt="" width="200px" v-if = "LoginUser.isImage"   @click = 'changeImage'>
           
        
 
          </el-form-item>

          
        </el-form-item>
        <el-form-item>
          <el-button size="medium" type="primary" @click="Login" style="width:100%;"></el-button>
           <el-button size="medium" type="primary" @click="adminLogin" style="width:80%;"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
        
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials  = true;
import { mapActions } from "vuex";
export default {
created(){
  
  

},

   
     data() {
    // 
    let validateName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
    
      if (value.lenght>=5) {
        this.$refs.ruleForm.validateField("checkPass");
        return callback();
      } else {
        return callback(new Error(""));
      }
    };
    // 
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error(""));
      }
    }
    return {
     LoginUser: {
        userName: "",
        pass: "",
        isImage:false,
      imageCode:'',
     
      },
      // ,validator(),trigger(),blur Input 
      rules: {
        name: [{ validator: validateName, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }]
      },
      imagesrc:''
      
    };
  },
  computed: {
    // vuexshowLogin
    isLogin: {
      get() {
          
        return this.$store.getters.getShowLogin;
         
      },
      set(val) {
      

       

          
        this.$refs["ruleForm"].resetFields();
       
        this.setShowLogin(val); 
      }
    }
  },
  watch:{
    isLogin(val){
          this.LoginUser.isImage = val
         if(val == true){
           
  axios({
      url: 'http://8.141.56.170:9100/images',
      method: 'get', 
      
      responseType: 'blob',    // blob                     
  }).then( (res) => {
     let src = window.URL.createObjectURL(res.data)//
     console.log(src)
     this.imagesrc = src;
  })
          
           
         }
         else{this.LoginUser.isImage = ''}
            
          }
          
    

  },
  methods: {
    ...mapActions(["setUser", "setShowLogin","setAdmin"]),
    Login() {
     
       
 var luser = {
              username: this.LoginUser.userName,
              password: this.LoginUser.pass,
              vercode:this.LoginUser.imageCode
            }

          axios.post("http://8.141.56.170:9100/user/login", {
              username: this.LoginUser.userName,
              password: this.LoginUser.pass,
              vercode:this.LoginUser.imageCode
            })
            .then(res1 => {
              console.log('login')
              console.log(res1.data)
             
              if (res1.data.status == 200) {
                // 
                this.isLogin = false;
                // 
               
                localStorage.setItem("user", luser.username);
                // vuex
                this.setUser(luser.username);
                console.log(this.$store.state.user.user)
               
                // 
                
                this.$alert('')
                this.isLogin = false;
              } else {
                // 
                this.$refs["ruleForm"].resetFields();
                this.$alert(res1.data.message)
                // 
                
              }
            })
          
        
    },
    adminLogin(){

      this.setAdmin(true)
      localStorage.setItem('isadmin',true)
      console.log(this.$store.state.user.isadmin)
      

      this.$router.push('/adminLogin')
      this.setShowLogin(false);
      
    },
    changeImage(){
      
      
    }
  }
};
</script>

<style lang="scss" scoped>

</style>