import Vue from 'vue'
import VueRouter from 'vue-router'
import collect from '../views/collect.vue'
import order from '../views/order.vue'

import Home from '../views/Home.vue'
import shoppingCar from '../views/shoppingCar'
import goods from '../views/goods'
import details from '../views/details.vue'
import adminLogin from '../admin/adminLogin.vue'
import adminHome from '../admin/adminHome.vue'
import addGood from '../views/addGood'
import add from '../views/add.vue'


Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
	VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
	}

const routes = [
  {path:'/order',
component:order},
{path:'/collect',
component:collect},
{
  path:'/addGood',
  component:addGood
},
{
  path:'/add',
  component:add
},
{path:'/',
component:Home},
{path:'/shoppingCar',
component:shoppingCar,
},
{path:'/goods',
component:goods},
{path:'/goods/details',
component:details},
{path:'/adminLogin',
component:adminLogin},
{path:'/adminHome',
component:adminHome,
children:[
  {path:'/usersList',
  component:()=>import('../admin/usersList.vue')
},
{path:'/ordersList',
component:()=>import('../admin/ordersList.vue')},
{path:'/cateList',
component:()=>import('../admin/cateList.vue')},
{path:'/goodsList',
component:()=>import('../admin/goodsList.vue'),
},
{path:'/rightsList',
component:()=>import('../admin/rightsList.vue')},
{path:'/rolesList',
component:()=>import('../admin/rolesList.vue')},
{path:'/addGoods',
component:()=>import('../admin/addGoods.vue')}
]

},

 


]

const router = new VueRouter({
  routes
})

export default router
