<template>
    <div class="goods" id="goods" name="goods">
   
    <div class="breadcrumb">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item to="/"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item v-if="search"></el-breadcrumb-item>
        <el-breadcrumb-item v-else></el-breadcrumb-item>
        <el-breadcrumb-item v-if="search">{{search}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>


    <!--  -->
    <div class="nav">
      <div class="product-nav">
        <div class="title"></div>
        <el-tabs v-model="activeName" type="card">
          <el-tab-pane
            v-for="item in categoryList"
            :key="item"
            :label="item"
            :name="''+item"
          />
        </el-tabs>
      </div>
    </div>
  

    <!--  -->
    <div class="main">
      <div class="list">
        <List :list="product" v-if="product.length>0"></List>
        <div v-else class="none-product"></div>
      </div>
      <!--  -->
      <div class="pagination">
        <el-pagination
          background
          layout="prev, pager, next"
          :page-size="pageSize"
          :total="total"
          @current-change="currentChange"
        ></el-pagination>
      </div>
       </div>
    
  </div>
</template>

<script>
import List from '../components/List'

export default {
  components:{List},
    
    data() {
        return {
              categoryList: [{
                category_name:'1',
                category_id:1
              },{category_name:'2',
                category_id:2}], //
      categoryID: [], // id
      product: "", // 
      productList: "",
      total: 20, // 
      pageSize: 15, // 
      currentPage: 1, //
      activeName: '', // id
      search: "" // 
            
        };
    },
     activated() {
    this.activeName = "-1"; 
    this.total = 0; 
    this.currentPage = 1; 
   
   
    // categoryID
    if (this.$route.query.categoryID != undefined) {
      this.categoryID = this.$route.query.categoryID;
      if (this.categoryID.length == 1) {
        this.activeName = "" + this.categoryID[0];
      }
      return;
    }
    // search
    if (this.$route.query.search != undefined) {
      this.search = this.$route.query.search;
    }
  },
    mounted() {
        
    },
    created(){
         // 
    this.getCategory();
    },
    watch:{
        // id
    activeName: function(val) {
     
      
        this.categoryID = val;
      
      // 
      this.total = 0;
      this.currentPage = 1;
      // 
      this.$router.push({
        path: "/goods",
        query: { categoryID: this.categoryID }
      });
    },
    search: function(val) {
      if (val != "") {
        this.getProductBySearch(val);
      }
    },
     categoryID: function() {
      this.getData();
      this.search = "";
    },
    // 
     $route: function(val) {
      if (val.path == "/goods") {
        if (val.query.search != undefined) {
          this.activeName = "-1";
          this.currentPage = 1;
          this.total = 10;
          this.search = val.query.search;
        }
      }
    }
    },
    methods: {
        bcaktop(){
            document.documentElement.scrollTop = 0
        },
        // 
    currentChange(currentPage) {
      this.currentPage = currentPage;
      if (this.search != "") {
        this.getProductBySearch();
      } else {
        this.getData();
      }
      this.backtop();
    },
     // 
    getCategory() {
      this.$axios.get('http://8.141.56.170:9100/product/getallcategorys').then(res=>{
     
          const    cate = res.data.data
          this.categoryList = cate;
       
      })
   
    },
     // 
    getData() {
      console.log(this.categoryID)
         this.$axios.post('http://8.141.56.170:9100/index/product',{
           category:this.categoryID
         }).then(res=>{
           
           this.product = res.data.data
       
           
          
         })
      
    },
     getProductBySearch() {
       console.log('search')
       this.$axios.get('http://8.141.56.170:9100/product/search/'+this.currentPage+'/'+this.search).then(res=>{
         console.log(res)
         this.product = res.data.data
         this.total = res.data.data.length
       })
    
    }
    
  
        
    },
};
</script>

<style >
.goods {
  background-color: #f5f5f5;
}
/* CSS */
.el-tabs--card .el-tabs__header {
  border-bottom: none;
}
.goods .breadcrumb {
  height: 50px;
  background-color: white;
}
.goods .breadcrumb .el-breadcrumb {
  width: 1225px;
  line-height: 30px;
  font-size: 16px;
  margin: 0 auto;
}
/* CSS END */

/* CSS */
.goods .nav {
  background-color:white;
  height:55px;
  line-height: 55px;

}
.goods .nav .product-nav {
  width: 1225px;
  height: 55px;
  line-height: 55px;
  margin: 0 auto;
}
.nav .product-nav .title {
  width: 50px;
  font-size: 20px;
  font-weight: 700;
  float: left;
  margin-top: 40px;
  margin-bottom: 0;
}
/* CSS END */

/* CSS */
.goods .main {
  margin: 0 auto;
  max-width: 1225px;
}
.goods .main .list {
  min-height: 650px;
  padding-top: 14.5px;
  margin-left: -13.7px;
  overflow: auto;
}
.goods .main .pagination {
  height: 50px;
  text-align: center;
}
.goods .main .none-product {
  color: #333;
  margin-left: 13.7px;
}

</style>